.group{position:relative;margin:45px 0}.group .form-input{width:100%;font-size:12px;padding:10px 10px 10px 8px;background-color:#fff;border-color:#ced4da;border-radius:4px;border-style:solid solid solid solid;border-width:1px 1px 1px 1px;color:#484f56}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:#000}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:13px;font-weight:normal;position:absolute;pointer-events:none;left:12px;top:12px;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}
